.tabContent {
  color: black;
  overflow: scroll;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  background-color: white;
  border: 1px solid black;
  height: calc(100% - 190px);
  margin-top: -1px;
  background: -webkit-gradient(linear, left top, left bottom, from(white), color-stop(84%, white), to(#ccc));
  background: -webkit-linear-gradient(white 0%, white 84%, #ccc 100%);
  background: -o-linear-gradient(white 0%, white 84%, #ccc 100%);
  background: linear-gradient(white 0%, white 84%, #ccc 100%);

}



